/* styles.css */
body {
  font-family: Arial, sans-serif;
  background-color: #f8f9fa;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
}

.world-clock-container {
  background-color: #ffffff;
  border-radius: 10px;
  padding: 20px;
  text-align: center;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

select {
  margin-top: 10px;
  padding: 8px;
  font-size: 16px;
}

.clock {
  font-size: 32px;
  margin-top: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.globe-icon {
  font-size: 48px;
  margin-right: 10px;
}